body, html, #root {
  margin: 0;
  padding: 0;
  height: 100%;
  background-color: #F3F3F3;
  color: white;
  font-family: sans-serif;
}
.app {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.content {
  flex: 1;
  padding: 20px;
  overflow-y: auto;
}

.page {
  text-align: center;
  padding-top: -250px;
}

.bottom-menu {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: #212121;
  padding: 10px 0;
}

.menu-item {
  margin-left: 2.8em;
  flex: 1;
  text-align: center;
  padding: 5px;
}

.icon {
  width: 28px;
  height: 28px;
  transition: transform 0.2s ease;
}

