.play-now-button:after {
 content: '';
 position: absolute;
 left: 0;
 right: 0;
 bottom: 0;
 box-shadow: 0px 0 0 0 white;
 transition: all 2s ease;
}

.play-now-button:hover:after {
 box-shadow: 0px -13px 56px 12px #ffffffa6;
}

.play-now-button span {
 position: absolute;
 width: 100%;
 font-size: 15px;
 font-weight: bold;
 left: 50%;
 top: 39%;
 letter-spacing: 3px;
 text-align: center;
 transform: translate(-50%,-50%);
 color: black;
 transition: all 2s ease;
}

.play-now-button:hover span {
 color: white;
}

.play-now-button:before {
 content: '';
 position: absolute;
 width: 0;
 height: 100%;
 background-color: black;
 left: 50%;
 top: 50%;
 transform: translate(-50%, -50%);
 transition: all 1s ease;
}

.play-now-button:hover:before {
 width: 100%;
}


/* Исправим контейнер */
.start-screen {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: white;
}

/* Когда кнопка нажата */
.play-now-button.clicked:before {
  width: 100%;
}

.play-now-button.clicked span {
  color: white;
}

.play-now-button {
  outline: none !important;
  box-shadow: none !important;
  -webkit-tap-highlight-color: transparent; /* Removes tap glow on mobile */
  user-select: none;
}

.play-now-button:focus,
.play-now-button:focus-visible,
.play-now-button:active {
  outline: none !important;
  box-shadow: none !important;
}




.stepper-overlay {
  position: fixed;
  z-index: 999;
  background-color: white; /* черный с прозрачностью 0.5 */
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  padding: 2em;
  overflow-y: auto;
}

.stepsinput {
  padding-left: 15px;
  font-weight: bold;
  border-width: 1px;
  
}

.stepsinput::placeholder {
  color: grey;
}